/* Project.js style */
.project-carousel {
  text-align: center;
}

.project-card {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  position: relative;
  padding: 0 10px;
}

.arrow {
  font-size: 2rem;
  background: none;
  border: none;
  cursor: pointer;
  color: #333333;
  z-index: 10;
}

.project-content {
  display: flex;
  align-items: center;
  border: none;
  padding: 20px;
  max-width: 1200px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  flex-wrap: wrap;
}

.project-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 30px;
  flex: 1 1 100%;
}

.project-image img {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: auto;
  object-fit: cover;
  margin-bottom: 10px;
}

.project-info {
  text-align: left;
  flex: 1 1 100%;
  margin-top: 20px;
  padding: 0 10px;
}

/* Visit Project */
.visit-button {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  text-align: center;
  font-weight: 500;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.visit-button:hover {
  background-color: #0056b3;
}


@media (max-width: 768px) {
  .project-content {
    flex-direction: column;
    padding: 15px;
  }

  .project-image {
    margin-right: 0;
    width: 100%;
  }

  .project-info {
    text-align: center;
    padding: 0;
  }

  .arrow {
    font-size: 1.5rem;
  }
}
